import React from "react"
import { Link } from "gatsby"
import filler from "../images/filler.jpeg"

function Function(props) {
  const path = "/projects/" + props.url

  return (
    <Link to={path} className="software-row">
      <div className="software-top">
        <p className="software-row-title">{props.name}</p>
        <p className="award">{props.award}</p>
      </div>

      <div className="software-img">
        <img alt={props.name} src={props.src ? props.src : filler} />
      </div>
    </Link>
  )
}

export default Function
